.item-add-form {
    margin-top: .5rem;
}

.item-add-input {
    background-color: #d7f1ff;
    color: black;
    font: italic bold 1.3rem/22px "GOST Common"
}

.item-add-btn {
    min-width: 100px;
    color: white;
    border: 1px solid #FB8F3D;
    background: #fdb32e;
    margin-left: .5rem;

}


.item-add-btn:hover {
    border: 1px solid #EB5200;
    background: #fd9915;
    box-shadow: 0 1px #EFEFEF;
}

.item-add-btn:active {
    box-shadow: inset 0 1px 1px rgba(0,0,0,0.3);
}