.shoplist-item-pre {
    line-height: 40px;
    transform: scale(2);
    cursor: pointer;
    margin-left: 2.25rem;
}

.shoplist-item-label {
    margin-left: .75rem;
    line-height: 30px;
    cursor: pointer;
    user-select: none;
    font-size: 1.5rem;
}

.shoplist-item.done .shoplist-item-label {
    text-decoration: line-through;
}

.shoplist-item.important .shoplist-item-label {
    font-weight: bold;
    color: steelblue;
}

.trash {
    margin-right: 1.5rem;
    width: 3rem;
}

.edit {
    margin-right: 1rem;
    width: 3rem;
}

.fa-trash {
    color: #7a090c;
}

.fa-pencil {
    color: #337a19;
}
