.app-header {
    margin: 0 auto;
    max-width: 500px;
    background-color: #ffffff;
    border-bottom: 6px #95b816 dashed;
    text-align: center;
}

.app-header h1 {
    flex-grow: 1;
    margin-bottom: 0;
}

.app-header p {
    font-size: 1.2rem;
    color: #19000c;
    margin-bottom: 0;
    border-bottom: 4px #bdb119 dashed;
}